import { useState } from "react"
import { useSelector } from 'react-redux'

// ** Invoice List Sidebar
import Sidebar from './Sidebar'

// ** Columns
import { columns } from './columns'

// ** Store & Actions
import { getAllUsers, getUserList } from '@store/Users/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const UsersList = () => {
  // ** States
  const [currentPlan, setCurrentPlan] = useState({ value: '', label: 'Select Plan' })
  const [currentStatus, setCurrentStatus] = useState({ value: '', label: 'Select Status' })
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const { allData, data, total } = useSelector(state => state.users)
  const { rolesConfig } = useSelector(state => state.roles)
  const { membership: membershipConfig, status: statusConfig } = rolesConfig.user

  const planOptions = [{ value: '', label: 'Select Plan' }]
  for (const mKey in membershipConfig) {
    planOptions.push({ value: mKey, label: membershipConfig[mKey] })
  }
  const statusOptions = [{ value: '', label: 'Select Status' }]
  for (const sKey in statusConfig) {
    statusOptions.push({ value: sKey, label: statusConfig[sKey] })
  }
  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const customFilters = () => {
    return [
      {
        key: 'membershipType',
        options: planOptions,
        value: currentPlan,
        onChange: setCurrentPlan
      },
      {
        key: 'status',
        options: statusOptions,
        value: currentStatus,
        onChange: setCurrentStatus
      }
    ]
  }

  const customSearch = () => {
    return {
      membershipType: currentPlan.value,
      status: currentStatus.value
    }
  }

  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={allData}
        data={data}
        total={total}
        columns={columns}
        filters={customFilters()}
        searchParams={customSearch()}
        getList={getUserList}
        getAllList={getAllUsers}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New User'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default UsersList
