// ** React Imports
import PropTypes from "prop-types"
import React, { Fragment, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'

// ** Third Party Components
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, CardBody, CardHeader, CardTitle, Row } from 'reactstrap'

// ** Custom Components
import TableHeader from '@components/table/TableHeader'
import TableFilter from '@components/table/TableFilter'
import TablePagination from '@components/table/TablePagination'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

const CarhootTable = (props) => {
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)

  const {
      allData,
      data,
      columns,
      filters,
      getAllList,
      getList,
      searchParams,
      total,
      headerActionHandler,
      headerActionLabel,
      customHeaderLabel,
      tableLabel
  } = props,
    dispatch = useDispatch()

  // ** Get data on mount
  useEffect(() => {
    dispatch(getAllList())
    dispatch(
      getList({
        ...searchParams,
        page: currentPage,
        perPage: rowsPerPage,
        q: searchTerm
      })
    )
  }, [dispatch])

  // ** Function in get data on page change
  const handlePagination = page => {
    dispatch(
      getList({
        ...searchParams,
        page: page.selected + 1,
        perPage: rowsPerPage,
        q: searchTerm
      })
    )
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = e => {
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getList({
        ...searchParams,
        page: 1,
        perPage: value,
        q: searchTerm
      })
    )
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleSearchTerm = val => {
    setSearchTerm(val)
    setCurrentPage(1)
    dispatch(
      getList({
        ...searchParams,
        page: 1,
        perPage: rowsPerPage,
        q: val
      })
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      ...searchParams,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (data.length > 0) {
      return data
    } else if (data.length === 0 && isFiltered) {
      return []
    } else {
      return allData.slice(0, rowsPerPage)
    }
  }

  const CustomPagination = () => (
    <TablePagination
      total={total}
      currentPage={currentPage}
      rowsPerPage={rowsPerPage}
      handlePagination={page => handlePagination(page)}
    />
  )

  const customFilters = () => {
    return filters.map((filter) => {
      return {
        ...filter,
        onChange: (data) => {
          filter.onChange(data)
          setCurrentPage(1)
          dispatch(
            getList({
              ...searchParams,
              [filter.key]: data.value,
              page: 1,
              perPage: rowsPerPage,
              q: searchTerm
            })
          )
        }
      }
    })
  }

  return (
    <Fragment>
      <TableFilter
        customTitle={customHeaderLabel}
        filters={customFilters()}
      />
      <Card>
        {
          tableLabel !== '' && <CardHeader>
            <CardTitle tag='h4'>{tableLabel}</CardTitle>
          </CardHeader>
        }
        <CardBody>
          <DataTable
            noHeader
            pagination
            subHeader
            responsive
            paginationServer
            columns={columns}
            sortIcon={<ChevronDown />}
            className='react-dataTable'
            paginationComponent={CustomPagination}
            data={dataToRender()}
            subHeaderComponent={
              <TableHeader
                toggleSidebar={headerActionHandler}
                handlePerPage={handlePerPage}
                rowsPerPage={rowsPerPage}
                searchTerm={searchTerm}
                handleFilter={handleSearchTerm}
                buttonLabel={headerActionLabel}
              />
            }
          />
        </CardBody>
      </Card>
    </Fragment>
  )
}

export default CarhootTable

// ** Default Props
CarhootTable.defaultProps = {
  allData: [],
  data: [],
  columns: [],
  total: 0,
  searchParams: {},
  filters: [],
  getList: (data) => false,
  getAllList: () => false,
  headerActionHandler: () => false,
  headerActionLabel: '',
  customHeaderLabel: 'Search Filter',
  tableLabel: ''
}

// ** PropTypes
CarhootTable.propTypes = {
  allData: PropTypes.array.isRequired,
  data: PropTypes.array.isRequired,
  columns: PropTypes.array.isRequired,
  total: PropTypes.number.isRequired,
  searchParams: PropTypes.object,
  filters: PropTypes.array,
  getList: PropTypes.func.isRequired,
  getAllList: PropTypes.func.isRequired,
  headerActionHandler: PropTypes.func,
  headerActionLabel: PropTypes.string,
  customHeaderLabel: PropTypes.string,
  tableLabel: PropTypes.string
}
