// ** React Import
import { useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'

// ** Custom components
import UILoader from '@components/ui-loader'
import Sidebar from '@components/sidebar'
import InputGroup from '@components/input-group'

// ** Utils

// ** Third Party Components
import classnames from 'classnames'
import { Button, Form, FormGroup } from 'reactstrap'

// ** Store & Actions
import { addUser, userFormChange } from '@store/Users/actions'

const SidebarNewUsers = ({
  open,
  toggleSidebar
}) => {
  const formEl = useRef(null)

  const { formErrors, isLoading, userFormData } = useSelector(state => state.users),
    dispatch = useDispatch()
  const { rolesConfig } = useSelector(state => state.roles)

  const handleFormChange = (name, value) => {
    dispatch(userFormChange(name, value))
  }

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(addUser())
  }

  const { membership: membershipConfig } = rolesConfig.user
  const membershipOptions = Object.keys(membershipConfig).map((mKey) => {
    return (
      <option key={mKey} value={mKey}>{membershipConfig[mKey]}</option>
    )
  })

  return (
    <Sidebar
      size='lg'
      open={open}
      title='New User'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit} innerRef={formEl}>
        <InputGroup
          type={'text'}
          label={'First Name'}
          error={formErrors['firstName']}
          name={'firstName'}
          placeholder={'First Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.firstName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['firstName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Last Name'}
          error={formErrors['lastName']}
          name={'lastName'}
          placeholder={'Last Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.lastName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['lastName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Username'}
          error={formErrors['userName']}
          name={'userName'}
          placeholder={'userName'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.userName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['userName'] })}
        />
        <InputGroup
          type={'email'}
          label={'Email'}
          error={formErrors['email']}
          name={'email'}
          placeholder={'Email'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.email}
          required={true}
          className={classnames({ 'is-invalid': formErrors['email'] })}
        />
        <InputGroup
          type={'select'}
          label={'Country Code'}
          error={formErrors['countryCode']}
          name={'countryCode'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.countryCode}
          required={true}
          className={classnames({ 'is-invalid': formErrors['countryCode'] })}
        >
          <option value=''>Select Country Code</option>
          <option value='254'>+254 Kenya</option>
        </InputGroup>
        <InputGroup
          type={'number'}
          label={'Contact'}
          error={formErrors['phoneNumber']}
          name={'phoneNumber'}
          placeholder={'Contact'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.phoneNumber}
          required={true}
          className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
        />
        <InputGroup
          type={'password'}
          label={'Password'}
          error={formErrors['password']}
          name={'password'}
          placeholder={'Password'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.password}
          required={true}
          className={classnames({ 'is-invalid': formErrors['password'] })}
        />
        <InputGroup
          type={'select'}
          label={'User Plan'}
          error={formErrors['membershipType']}
          name={'membershipType'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userFormData.membershipType}
          className={classnames({ 'is-invalid': formErrors['membershipType'] })}
        >
          <option value='free'>Select Plan</option>
          {membershipOptions}
        </InputGroup>
        <FormGroup row className='mr-0 ml-0'>
          <UILoader blocking={isLoading} className='mr-1'>
            <Button type='submit' color='primary'>
              Submit
            </Button>
          </UILoader>
          <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
            Cancel
          </Button>
        </FormGroup>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewUsers
