import PropTypes from "prop-types"
import { Card, CardBody, CardHeader, CardTitle, Col, Row } from "reactstrap"
import Select from "react-select"

import { randomString } from '@utils/randomString'
import { selectThemeColors } from '@utils/Utils'

const TableFilter = (props) => {
  const { customTitle, filters } = props

  const renderFilters = () => {
    return filters.map((filter) => {
      const { options, value, onChange } = filter
      return <Col md='4' key={randomString(32)}>
        <Select
          theme={selectThemeColors}
          isClearable={false}
          className='react-select'
          classNamePrefix='select'
          options={options}
          value={value}
          onChange={data => onChange(data)}
        />
      </Col>
    })
  }

  if (filters.length < 1) return null

  return (
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>{customTitle}</CardTitle>
      </CardHeader>
      <CardBody>
        <Row>
          {renderFilters()}
        </Row>
      </CardBody>
    </Card>
  )
}

export default TableFilter

// ** Default Props
TableFilter.defaultProps = {
  customTitle: 'Search Filter',
  filters: []
}

// ** PropTypes
TableFilter.propTypes = {
  customTitle: PropTypes.string,
  filters: PropTypes.array
}
