import { isDefined } from "@utils/isDefined"

const formatPhone = (phone) => {
  if (!isDefined) return ''

  const x = phone.toString().replace(/\D/g, '').match(/(\d{3})(\d{3})(\d{3})(\d{3})/)

  if (x.length < 5) return ''

  return `+${ x[1] } ${ x[2] } ${ x[3] } ${ x[4] }`
}
export default formatPhone
