// ** React Imports
import { Link } from "react-router-dom"
import PropTypes from 'prop-types'

// ** Custom Components
import Avatar from '@components/avatar'

const UserAvatar = (props) => {
  const { name, photo } = props
  const stateNum = Math.floor(Math.random() * 6),
    states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
    color = states[stateNum]

  if (photo !== undefined && photo.length) {
    return <Avatar className='mr-1' img={photo} width='32' height='32' />
  } else {
    return <Avatar color={color || 'primary'} className='mr-1' content={name} initials />
  }
}

const UserListAvatar = (props) => {
  const { name, photo, viewHandler, viewLink } = props
  return <div className='d-flex justify-content-left align-items-center'>
    <UserAvatar
      name={name}
      photo={photo}
    />
    <div className='d-flex flex-column'>
      <Link
        to={viewLink}
        className='user-name text-truncate mb-0'
        onClick={viewHandler}
      >
        <span className='font-weight-bold'>{name}</span>
      </Link>
    </div>
  </div>
}

export default UserListAvatar

// ** Default Props
UserListAvatar.defaultProps = {
  name: '',
  photo: '',
  viewLink: '',
  viewHandler: () => false
}

// ** PropTypes
UserListAvatar.propTypes = {
  name: PropTypes.string.isRequired,
  photo: PropTypes.string,
  viewLink: PropTypes.string.isRequired,
  viewHandler: PropTypes.func
}
