// ** React Imports
import { useSelector } from 'react-redux'

// ** Custom Components
import UserListAvatar from '@components/user-avatar/UserListAvatar'
import TableAction from '@components/table/TableAction'

// ** Store & Actions
import { getUser } from '@store/Users/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge } from 'reactstrap'
import { Archive, Award, Bell, FileText, User, UserPlus, Users } from 'react-feather'

import { formatPhone } from '@helper'

// ** Renders Role Columns
const renderMembership = row => {
  const { rolesConfig } = useSelector(state => state.roles)
  const { membership: membershipConfig } = rolesConfig.user
  const roleObj = {
    free: {
      class: 'text-primary',
      icon: User
    },
    bronze: {
      class: 'text-secondary',
      icon: UserPlus
    },
    gold: {
      class: 'text-warning',
      icon: Award
    },
    platinum: {
      class: 'text-danger',
      icon: Users
    }
  }

  const Icon = roleObj[row.membershipType] ? roleObj[row.membershipType].icon : Bell

  return (
    <span className='text-truncate text-capitalize align-middle'>
      <Icon size={18} className={`${roleObj[row.membershipType] ? roleObj[row.membershipType].class : ''} mr-50`} />
      {membershipConfig[row.membershipType]}
    </span>
  )
}

const statusObj = {
  pending: 'light-warning',
  active: 'light-success',
  deactivated: 'light-secondary',
  deleted: 'light-danger'
}

export const columns = [
  {
    name: 'User',
    minWidth: '297px',
    selector: 'fullName',
    sortable: true,
    cell: row => (
      <UserListAvatar
        name={`${row.firstName} ${row.lastName}`}
        photo={row.profilePictureURL}
        viewHandler={() => store.dispatch(getUser(row._id))}
        viewLink={`/users/view/${row._id}`}
      />
    )
  },
  {
    name: 'Email',
    minWidth: '229px',
    selector: 'email',
    sortable: true,
    cell: row => row.email
  },
  {
    name: 'Phone Number',
    minWidth: '229px',
    selector: 'phoneNumber',
    sortable: true,
    cell: row => <span className='text-capitalize'>
      {formatPhone(row.phoneNumberStr)}
    </span>
  },
  {
    name: 'Membership',
    minWidth: '172px',
    selector: 'membershipType',
    sortable: true,
    cell: row => renderMembership(row)
  },
  {
    name: 'Status',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => {
      const { rolesConfig } = useSelector(state => state.roles)
      const { status: statusConfig } = rolesConfig.user
      return (
        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
          {statusConfig[row.status]}
        </Badge>
      )
    }
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => {
      const actions = [
        {
          link: `/users/view/${row._id}`,
          handler: () => store.dispatch(getUser(row._id)),
          label: 'Details',
          icon: (<FileText size={14} className='mr-50' />)
        },
        {
          link: `/users/edit/${row._id}`,
          handler: () => store.dispatch(getUser(row._id)),
          label: 'Edit',
          icon: (<Archive size={14} className='mr-50' />)
        }
      ]
      return (
        <TableAction actions={actions} />
      )
    }
  }
]
